import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

import { IFilterConfiguration } from '../public-api';
import _ from 'lodash'

@Component({
  selector: 'ngx-filter',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent implements OnInit {
  @Input() filterConfig: IFilterConfiguration[] = []
  @Input() lang: string = "fr"
  @Input() withRefresh = true
  @Input() searchType: 'search-group' | 'search-text' = 'search-group'
  @Input() datas: any[] = []
  @Output() onFilter = new EventEmitter<any>()
  @Output() onRefresh = new EventEmitter<any>()

  ngOnInit() {
  }
  onFilterData(e) {
    console.log('---------ON-FILTER-------------', e);
    this.onFilter.emit(e)
  }
  refresh(e) {
    console.log('---------ON-REFRESH-------------', e);
    this.onRefresh.emit(e)
  }

}
